const {Subject} = require('await-notify');


export function initDebug(ctx: ExtensionContext, docsel: DocumentFilter): void {
	// 
	const lng = docsel.language ?? '';
	ctx.subscriptions.push(debug.registerDebugConfigurationProvider(lng, {
		resolveDebugConfiguration(_folder: WorkspaceFolder | undefined, cfg: DebugConfiguration, _token?: CancellationToken): ProviderResult<DebugConfiguration> {
			const ed = window.activeTextEditor;
			if (ed && ed.document.languageId === 'text') {
				cfg = {...cfg, ...{
					name		: 'Chrome',
					type		: 'skynovel',
					request		: 'launch',
	//				program		: '${file}',	// TODO: 
					program		: '${workspaceFolder}/asm.txt',
					mode		: '~/Documents/MacHD2/_Famibee/SKYNovel/sn_extension/core/src/server.js',
					runtime		: 'node',
					stopOnEntry	: true
				}};
			}

			if (! cfg.program) return window.showInformationMessage('Cannot find a program to debug').then(_=> undefined);	// abort launch

			return cfg;
		}
	}));

	// 
	let daSrv: Server | null;
	const dadf: DebugAdapterDescriptorFactory = {
		S_createDebugAdapterDescriptor(_ss: DebugSession, _daExe: DebugAdapterExecutable | undefined): ProviderResult<DebugAdapterDescriptor> {
			if (! daSrv) {
console.log(`fn:DebugSS.ts line:60 createDebugAdapterDescriptor`);
				daSrv = createServer(socket=> {
					const ss = new DebugAdapter();
					ss.setRunAsServer(true);
					ss.start(<NodeJS.ReadableStream>socket, socket);
				}).listen(0);	// start listening on a random port
			}
			// make VS Code connect to debug server
			return new DebugAdapterServer((<AddressInfo>daSrv.address()).port);
		},	
		dispose() {daSrv?.close(); daSrv = null;},
/*
	export class DebugAdapterServer {
		readonly port: number;
		readonly host?: string;
		constructor(port: number, host?: string);
	}
*/
		//Q_
		createDebugAdapterDescriptor(_ss: DebugSession): ProviderResult<DebugAdapterDescriptor> {
			return new DebugAdapterInlineImplementation(new DebugAdapter());
		},	// TODO: delete

		E_createDebugAdapterDescriptor(_ss: DebugSession, daExe: DebugAdapterExecutable | undefined): ProviderResult<DebugAdapterDescriptor> {
//			return daExe ?? new DebugAdapterExecutable('npm', ['run', 'dbg_skynovel']);
			return daExe ?? new DebugAdapterExecutable('node_modules/.bin/webpack-dev-server', ['--config', './core/wds.dbg.config.js']);
		}
	};

	ctx.subscriptions.push(debug.registerDebugAdapterDescriptorFactory(lng, dadf));
	if ('dispose' in dadf) ctx.subscriptions.push(dadf);
}
